<template>
    <transition name='fade'>
        <div :class="type" class="toast">
            {{message}}
        </div>
    </transition>
</template>

<script>
export default {
  props: {
    message: {
      type: String
    },
    type: {
      type: String,
      default: 'success'
    },
    duration: {
      type: [Number, String],
      default: 1000
    }
  }
}
</script>

<style lang='stylus' scoped>
    .toast
        position fixed
        top 50%
        left 50%
        height 35px
        line-height 35px
        min-width 150px
        padding 0 20px
        text-align center
        font-size 14px
        color #ffffff
        box-sizing border-box
        transform translate(-50%, -50%)
        z-index 26
        &.success
            background-color rgba(36,207,95,1)
        &.fail
            background-color #F1524A
    .fade-enter-active, .fade-leave-active
        transition opacity .3s
    .fade-enter, .fade-leave-to
        opacity 0
</style>



