<template>
  <div @click='handleCheckAll' class="g-checkbox" :class="{active: checked||checkedAll,'has-machines':hasMachines}"
       @blur="menuBlur" ref="menu" tabindex="0">
    <span class="checkbox-box" :class='{active: checked,activeHalf: checked&&!checkedAll}'></span>
    <span class="checkbox-label text-ellipsis">{{label}}</span>
    <div class="drop-down-box" @click.stop="dropdown">
      <span class="drop-down"></span>
    </div>
    <ul class="terminal-list" v-if="isOpening" @click.stop>
      <template v-if="hasMachines">
        <li v-for="(machine,index) in machines" :key="index" @click.stop="handleCheck(machine.id)">
          <span class="checkbox-box" :class="{active:selected.indexOf(machine.id)!==-1}"></span>
          <span class="text-ellipsis">{{machine.name}}</span>
        </li>
      </template>
      <template v-else>
        <li class="empty-li">
          <span>没有终端机</span>
        </li>
      </template>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    label: String,
    machines: Array,
    selected: Array
  },
  data () {
    return {
      isOpening: false
    }
  },
  computed: {
    hasMachines: function () {
      return this.machines && this.machines.length !== 0
    },
    checked: function () {
      let length = 0
      this.machines.forEach((machine) => {
        if (this.selected.indexOf(machine.id) !== -1) {
          length++
        }
      })
      return length !== 0
    },
    checkedAll: function () {
      let length = 0
      this.machines.forEach((machine) => {
        if (this.selected.indexOf(machine.id) !== -1) {
          length++
        }
      })
      return (length !== 0) && (length === this.machines.length)
    }
  },
  methods: {
    handleCheckAll () {
      if (this.machines.length === 0) {
        this.dropdown()
      } else {
        if (this.checkedAll || !this.checked) {
          this.machines.forEach((machine) => {
            this.$emit('change', machine.id)
          })
        } else {
          this.machines.forEach((machine) => {
            if (this.selected.indexOf(machine.id) === -1) {
              this.$emit('change', machine.id)
            }
          })
        }
      }
    },
    handleCheck (id) {
      this.$emit('change', id)
    },
    dropdown () {
      this.$refs.menu.focus()
      this.isOpening = !this.isOpening
    },
    menuBlur () {
      this.isOpening = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  .g-checkbox
    outline none
    display flex
    align-items center
    padding-left 10px
    width 136px
    height 35px
    line-height 35px
    border-radius 4px
    box-sizing border-box
    background-color #eaeaea
    cursor pointer
    position relative
    &.active
      color #ffffff
      background-color rgba(36, 207, 95, 1)
      & > .checkbox-box
        border 1px solid white
    &.has-machines:before
      content ''
      position absolute
      top 0
      left 0
      height 100%
      width 3px
      background #24cf5f
      border-top-left-radius 4px
      border-bottom-left-radius 4px
    .checkbox-label
      font-size 12px
      line-height 12px
      flex 1

  .checkbox-box
    position relative
    display inline-block
    margin-right 7px
    width 15px
    height 15px
    border-radius 3px
    border 1px solid #cccccc
    background-color #ffffff
    &.active:after
      position absolute
      top 4px
      left 3px
      display inline-block
      content: ''
      height 4px
      width 7px
      border 1px solid rgba(36, 207, 95, 1)
      border-top none
      border-right none
      transform rotate(-45deg)
    &.active.activeHalf:after
      border 1px solid rgba(36, 207, 95, 1)
      transform: rotate(0deg);
      height: 2px;
      top 6px

  .drop-down-box
    width 24px
    height 100%
    outline none

  .drop-down
    position absolute
    width 7px
    height 7px
    top: -5px
    right: 13px
    bottom: 0
    margin: auto 0
    border-left: 1px solid #FFFFFF
    border-bottom: 1px solid #FFFFFF
    transform rotate(-45deg)

  .terminal-list
    position absolute
    left 0
    top 100%
    z-index 5
    line-height 26px
    color #333333
    outline none
    width 100%
    background: white;
    border: 1px solid #EAEAEA;
    border-radius: 4px;
    padding 4px 0
    li
      display flex
      align-items center
      padding 0 10px

  .empty-li
    color #B1B1B1
</style>



