<template>
  <div class="form-title" :class="{'red-title':($props.red==='')||(!!$props.red)}">
    <slot/>
  </div>
</template>
<script>
export default {
  props: ['red']
}
</script>
<style lang='stylus' scoped>
  .form-title
    padding 20px
    position relative
    font-size 16px
    color #666666
    &.red-title:before
      background #F1524A
    &:before
      content ''
      position absolute
      top 0
      left 0
      bottom 0
      margin auto 0
      width 5px
      height 23px
      background #24CF5F
      border-radius 0px 3px 3px 0px
</style>



