<template>
  <div class="breadcrumb-container">
    <ul class="breadcrumb-list">
      <slot/>
    </ul>
  </div>
</template>
<script>
export default {
  data () {
    return {}
  },
  methods: {}
}
</script>
<style lang='stylus' scoped>
  .breadcrumb-container
    height 60px
    padding-left 20px
    display flex
    align-items center
    border-bottom 1px solid #EAEAEA

  .breadcrumb-list
    display flex
    flex-direction row
    font-size 14px
    color #666666
    align-items center
    > li
      &:first-child
        font-size 18px
        cursor pointer
      &:after
        content ''
        position relative
        bottom 1px
        display inline-block
        background url('~@/assets/breadcrumb.png') no-repeat center
        background-size 100% 100%
        width 7px
        height 10px
        margin 0 10px
      &:last-child
        color #2162D2
        cursor default
        &:after
          display none
</style>



