<template>
  <modal v-if='serverData.length' class='edit-shop-modal'
         :modal-title="'选择终端机 ( '+this.userSelectedIdArr.length+'/'+this.machineLength+' ) '"
         @cancelModal='handleCancel'
         @confirmModal='handleConfirm'>
    <div class="modal-content-slot">
      <ul class="modal-content-left">
        <li v-for='(item, index) in serverData' :key='index'
            @click='currentCityIndex = index'
            :class="{active: index===currentCityIndex}"
            class="modal-content-left-item">
          {{item.stores_city}}
        </li>
      </ul>
      <div class="modal-content-right">
        <g-checkbox
          v-for='(item, index) in serverData[currentCityIndex].children' :key='index'
          :machines="item.machines"
          :label='item.stores_name'
          :selected="userSelectedIdArr"
          @change="checkboxChange">
        </g-checkbox>
      </div>
    </div>
  </modal>
</template>
<script>
import { getMachineListByUserIdForScreen } from 'api/terminal'
import Modal from 'components/Modal'
import Toast from 'components/Toast'
import GCheckbox from 'views/user/components/SelectTerminalCheckbox.vue'
import { Message } from 'element-ui'
import { getToken } from 'utils/tool'

export default {
  components: {Modal, Toast, GCheckbox},
  props: {
    createModalStatus: {
      type: Boolean,
      default: true
    },
    type: String,
    selected: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      serverData: [],
      currentCityIndex: 0,
      userSelectedIdArr: this.selected,
      machineLength: 0
    }
  },
  mounted () {
    this.token = getToken()
    this.stores = {}
    this.machines = {}
    this.getListData()
  },
  methods: {
    getList (publicData) {
      let getList
      let typeData = {}
      switch (this.type) {
        case 'screen':
          getList = getMachineListByUserIdForScreen
          break
        default:
          getList = getMachineListByUserIdForScreen
      }
      let propData = this.params
      return getList(Object.assign({}, publicData, typeData, propData))
    },
    getListData () {
      Promise.all([this.getList({'user_id': this.token.id})]).then(res => {
        if (res[0].code === '200') {
          let areaList = res[0].data
          let stores = []
          let machineLength = 0
          let finalListData = []
          areaList.forEach((item, index) => {
            let machines = (() => {
              let machines = []
              if (item.machines2.machine_id) {
                let machinesIdArr = item.machines2.machine_id.split(',')
                let machinesNameArr = item.machines2.machine_name.split(',')
                let machinesLength = machinesNameArr.length
                for (let i = 0; i < machinesLength; i++) {
                  let machine = {
                    name: machinesNameArr[i],
                    id: machinesIdArr[i],
                    storeId: item.stores_id,
                    storeName: item.stores_name
                  }
                  this.machines[machinesIdArr[i]] = machine
                  machines.push(machine)
                }
                machineLength = machineLength + machinesLength
              }
              return machines
            })()
            let store = {
              area_name: item.stores_city,
              stores_id: item.stores_id,
              stores_name: item.stores_name,
              machines
            }
            stores.push(store)
          })

          let tempListData = {}
          stores.forEach(item => {
            if (!tempListData.hasOwnProperty(item.area_name)) {
              tempListData[item.area_name] = {
                stores_city: item.area_name,
                children: []
              }
            }
            tempListData[item.area_name].children.push(item)
          })

          for (let key in tempListData) {
            finalListData.push(tempListData[key])
          }

          this.machineLength = machineLength
          this.serverData = finalListData
        } else if (res[0].code === '201') {
          Message.warning({
            message: '您的账号下没有分配门店，请确认后再试'
          })
        }
      })
    },
    checkboxChange (id) {
      let idArr = this.userSelectedIdArr
      let idIndex = idArr.indexOf(id)
      if (idIndex >= 0) {
        idArr.splice(idIndex, 1)
      } else {
        idArr.push(id)
      }
    },
    handleConfirm () {
      let lists = this.userSelectedIdArr.map(value => this.machines[value]).filter(v => !!v)
      this.$emit('confirm', lists)
    },
    handleCancel () {
      this.$emit('cancel')
    }
  }
}
</script>
<style lang="stylus" scoped>
  .edit-shop-modal /deep/ .modal-content
    padding 0

  .modal-content-slot
    display flex
    border-bottom 1px solid #eaeaea
    overflow hidden

  .modal-content-left
    padding-bottom 15px
    width 140px
    height 400px
    overflow auto
    line-height 14px
    font-size 14px
    text-align center
    border-right 1px solid #EAEAEA

  .modal-content-left-item
    margin-top 15px
    cursor pointer
    &.active
      color #217EE6

  .modal-content-right
    min-height 400px
    display flex
    flex-wrap wrap
    justify-content flex-start
    align-content flex-start
    padding 20px 23px
    width 676px
    height 400px
    overflow auto
    box-sizing border-box
    .g-checkbox
      margin 0 23px 18px 0
      &:nth-child(4n)
        margin-right 0
</style>



